import { IEvent } from '../../common/events/IEvent';

/**
 * Structure representing webpart - webpart events
 *
 * @alpha
 */
interface IWebPartEvent<T> extends IEvent<T> {

  /**
   * Source webpart name
   */
  source: string;

  /**
   * Target webpart name
   */
  target: string;
}

export default IWebPartEvent;
